.home {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: center;

  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
}

.pageWrapper {
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;}

  .section-wrapper {
      width: 96%;}


.headerContainer {
  width: auto;
  margin-left: 50px;
}

.headerContainer h1 {
  justify-content: center;
  align-items: center;
  position: relative;
  left: 800px;
  top: -55px;
  font-size: 90px;
  height: 60px;
  font-weight: 50;
  color: white;
}

.headerContainer p {
  padding: 0;
  font-size: 40px;
  position: relative;
  left: 680px;
  top: -70px;
  height: 60px;
  font-weight: lighter;
  color: white;
}

.headerContainer button {
  color: black;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  left: 880px;
  top: -100px;
  height: 60px;
  justify-content: center;
  background-color: #121619;
  height: 50px;
  width: 180px;
  color: white;
  text-decoration: none;
  font-size: 20px;
}

* {box-sizing:border-box}



@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

.home-section4 {
  width: 100%;
  padding: 20px;
  display: block;
  font-size: larger;
  background-color: lightgray;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
  font-family: 48px 'Allerta Stencil',  serif}
.home-section4 .main-heading .highlighted {
    color: darkmagenta;
    text-decoration-line: underline;
    
  }

.home-section4 .main-heading h4 {
    
    font-size: larger;
    text-decoration-line: underline;
    
  }
.home-section4 .instructors-section {
      box-sizing: border-box;
      width: 100%;
      display: flex;
      justify-content: space-between;
      background: black;
      align-items: center;
      min-height: 80vh;
      margin: 40px 0;
      padding: 10px;
      border-radius: 30px;
      position: relative;
      overflow: hidden;
     &::after {
          content: "";
          width: 50%;
          height: 70%;
          background: rgba(209, 170, 255, 0.49);
          position: absolute;
          top: 50%;
          transform: translateX(-50%);
          border-radius: 25px;
          left: 0;
          z-index: 1;

      }}

.home-section4 .instr-left {
          width: 30%;
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 15px;
          z-index: 2;}
.home-section4 h6 {
              text-align: left;
              display: block;
              font-family: Josefin Sans,sans-serif;
              font-size: 2vw;
              color: aliceblue;
          }
.home-section4 h1  {
        text-align: left;
        display: block;
        font-family: "Allerta Stencil",serif; 
        font-size: 2em;
        margin-block-start: 0.67em;
        margin-block-end: 0.67em;
        margin-inline-start: 0px;
        margin-inline-end: 0px;
        font-weight: bold;
        text-decoration-line: underline;

            
        }
.home-section4 .instructors-section {
          box-sizing: border-box;
          width: 100%;
          display: flex;
          justify-content: space-between;
          background: black;
          align-items: center;
          min-height: 80vh;
          margin: 40px 0;
          padding: 10px;
          border-radius: 30px;
          position: relative;
          overflow: hidden;
          
          &::after {
            content: "";
            width: 50%;
            height: 70%;
            background: rgba(209, 170, 255, 0.49);
            position: absolute;
            top: 50%;
            transform: translateX(-50%);
            border-radius: 25px;
            left: 0;
            z-index: 1;

        }}

.home-section4 .instr-right {
          width: 70%;
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 3;

      }
      * {box-sizing:border-box;
      }
      

.home-section4 .instr-right .owl-theme span{
          width: 70%;
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 5;

}
      
      @keyframes fade {
        from {opacity: .4}
        to {opacity: 1}
      }

.headerContainer button:hover {
  background-color: #07090a;
  cursor: pointer;
   
}

@media only screen and (max-width: 650px) {
  .home {
    justify-content: center;
    align-items: center;
  }
  .headerContainer h1,
  .headerContainer p {
  }

  .headerContainer {
    margin-left: 0px;
    border-radius: 10px;
    padding: 10px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #121619;
    color: white;
  }

  .headerContainer h1 {
    font-size: 40px;
    height: 30px;
    color: white;
  }

  .headerContainer p {
    font-size: 30px;
    color: white;
  }

  .headerContainer button {
    background-color: white;
    color: #121619;
    margin-bottom: 30px;
  }

  .headerContainer button:hover {
    background-color: rgb(225, 225, 225);
    color: #121619;
  }
}
