:root {
    --main-color: #ff9ef2;
    --secondary-color: rgb(186, 88, 255);
    --main-font: 'Josefin Sans', sans-serif;
    --secondary-font:'Allerta Stencil', sans-serif;
    --tertiary-font:'Julius Sans One', sans-serif;
}

.main-heading {
    h1 {
        font-size: 3em;
    }

    .highlighted {
        color: var(--secondary-color);
    }

    h4 {
        font-size: 1.5vw;
        margin: 20px 0;
    }
}

.highlighted-text {
    color: var(--secondary-color);
}

.owl-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .owl-prev,.owl-next {
        width: 50px !important;
        height: 50px !important;
        color: white !important;
        display: flex !important;
        justify-content: center !important;
        align-items: center !important;
        background: rgba(255, 255, 255, 0.319) !important;

        &:hover{
            background-color: white !important;
            color: black !important;
        }

    }
    .owl-prev{
        .nav-left-btn{
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

}

.link-btns{
    padding: 12px 17px;
    background: black;
    color: white;
    text-decoration: none;
    border: none;
    border-radius: 4px;
    font-weight: 800;
    cursor: pointer;
    display: inline-block;

    &:hover{
        background-color: var(--secondary-color);
        
    }

}