.navbar {
  width: 100%;
  height: 100px;
  background-color: #121619;
  display: flex;
  flex-direction: row;
}

.navbar .leftSide {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 150px;
}

.navbar .leftSide img {
  width: 150px;
  padding-top: 10px;
  position: absolute;
  left: 16px;
  
}

.navbar .rightSide {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar .rightSide a {
  position: relative;

}
.navbar .rightSide a .dropdownlinks{
  position: absolute;
  top: 110%;
  width: 200px;
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  translate:  0 20px 0;
  transition: 0.5s ease;
  padding: 7px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 15px;
  box-sizing: border-box;
  background: lightgray ;
  font-weight: bold;
  border-bottom: 1px solid rgba(208, 208, 208, 0.641);
  border-top: 1px solid rgba(208, 208, 208, 0.641);
}
.navbar .rightSide a .dropdownlinks a{
  color: black;
  text-decoration: none;
  margin: 0%;

}
.navbar .rightSide a:hover .dropdownlinks{
visibility: visible;
translate: 0;
opacity: 1;
transition: 0.5s ease;


}
.navbar .leftSide a {
  color: white;
  font-weight:  bolder;
  font-family : Copperplate, Papyrus, fantasy;
  text-decoration: none;
  font-size: 40px;
  margin: 20px;
}
.navbar a {
  color: white;
  text-decoration: none;
  margin: 20px;
}

.navbar .rightSide button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
}

.navbar .rightSide svg {
  font-size: 40px;
}

.navbar #open {
  padding-left: 0px;
}
.navbar #open img {
  display: none;
}

.navbar #close img {
  display: inherit;
}

.navbar #open .hiddenLinks {
  display: inherit;
  margin-left: 30px;
}

.navbar #close .hiddenLinks {
  display: none;
}

.navbar #open a {
  width: 70px;
  margin: 5px;
}
@media only screen and (max-width: 900px) {
  .navbar .rightSide a {
    width: 70px;
  }
  .navbar .leftSide {
    padding-left: 50px;
  }
}

@media only screen and (max-width: 600px) {
  .navbar .rightSide a {
    display: none;
  }

  .navbar .rightSide {
    justify-content: flex-end;
    padding-right: 50px;
  }

  .navbar .rightSide button {
    display: inherit;
  }
}

@media only screen and (min-width: 600px) {
  .navbar .rightSide button {
    display: none;
  }

  .hiddenLinks {
    display: none;
  }
}
